#admin .admin {
	padding-top: 40%;
}
.admin-form {
	margin-top: 2rem;
}

#admin .form, .freebie, .author{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-pack: center;
	    justify-content: center;
}

#admin .freebie, .author {
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex:1 1;
	    flex:1 1;
	background: white;
	padding: 3rem;
	min-width: 40%;
	margin-top: 2rem;
}

#admin input[type=text] {
	min-width: 12rem;
	width: 100%;
	margin-top: 1rem;
	margin-bottom: 2rem;
}

#admin input[type=text]::-webkit-input-placeholder {
	color: #7D849E;
}

#admin input[type=text]:-ms-input-placeholder {
	color: #7D849E;
}

#admin input[type=text]::-ms-input-placeholder {
	color: #7D849E;
}

#admin input[type=text]::placeholder {
	color: #7D849E;
}

#admin input[type=submit] {
	background: blue;
	color: white;
}

#admin input[type=submit]:hover{
	cursor: pointer;
}

label {
	width: 100%;
}


