.filter {
	background: none;
}

.imageCard {
	width: 300;
	height: 400;
}
.card__actions a img {
	display: inline-block;
	max-width: 1rem;
	max-height: 1rem;
	z-index: 2;
	margin-left: 1rem;
}

.card__actions a {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
}

.card__labels a {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	color: black;
	font-family: 'Larsseit';
	font-weight: normal;
	font-size: 0.8rem;
}

.card__labels a img {
	display: inline-block;
	max-width: 2rem;
	max-height: 2rem;
	margin-right: 1rem;
	z-index: 2;
	border-radius: 50px;
}

 
.card__heading, .card__details, .card__description {
	-ms-flex: 1 1 !important;
	    flex: 1 1 !important;
}

.card__actions {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	margin: 0 auto;
	width: 100% !important;

	background: 0 !important;
	padding: 2rem 0rem !important;
	bottom: 0;
	-webkit-transition: all 0.35s ease;
	-o-transition: all 0.35s ease;
	transition: all 0.35s ease;
}
.card__actions a {
	color: black;
	width: 87.5% !important;
	background: 0 !important;
	border-top: 1px solid #fafafa;
	display: -ms-flexbox;
	display: flex;
	padding: 0;
	margin:0 auto;
	-ms-flex-pack: start;
	    justify-content: flex-start;
}

.card__actions a:hover {
	color: rgba(0,0,0,0.50) !important;
}

.link-text {
	-ms-flex: 1 1;
	    flex: 1 1;
	text-align: left;
	color: linear-gradient(103.41deg, #0094FF -66.34%, #8000FF 102.08%) !important;

	}

.card__actions a:hover > .card__actions {
	width: 100% !important;
	background: -webkit-linear-gradient(346.59deg, #0094FF -66.34%, #8000FF 102.08%) !important;
	background: -o-linear-gradient(346.59deg, #0094FF -66.34%, #8000FF 102.08%) !important;
	background: linear-gradient(103.41deg, #0094FF -66.34%, #8000FF 102.08%) !important;
}

.card__actions:hover a{

	color: white;
	-webkit-transition: all 0.1s ease;
	-o-transition: all 0.1s ease;
	transition: all 0.1s ease;
}

.card__actions:hover {
  background: -webkit-linear-gradient(346.59deg, #0094FF -66.34%, #8000FF 102.08%) !important;
  background: -o-linear-gradient(346.59deg, #0094FF -66.34%, #8000FF 102.08%) !important;
  background: linear-gradient(103.41deg, #0094FF -66.34%, #8000FF 102.08%) !important;
  -webkit-transition: all 0.1s ease;
  -o-transition: all 0.1s ease;
  transition: all 0.1s ease;
  cursor: pointer;
}


.card__actions:hover .link-text {
	color: white !important;
		  -webkit-background-clip: text;
	  -webkit-text-fill-color: white;
	-webkit-transition: all 0.0001s ease;
	-o-transition: all 0.0001s ease;
	transition: all 0.0001s ease;
}



